@use 'base/variables' as v;
@use 'base/mixins' as m;
.header{
    background-color: darken(v.$morado, 5%);

    @include m.tablet { 
        &.fijo {
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            box-shadow: .1rem .2rem .3rem #333 ;
        }
    }

    h1{
        color: v.$blanco;
        text-align: center;
        @include m.tablet {
            margin: 0; 
        }
        
    }
    .contenido-header{
        @include m.tablet {
            padding: 2rem;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
}

.navegacion-principal{
    @include m.tablet {
        display: flex;
        gap: 2rem;
    }
    a{
            display: block;
            text-align: center;
            color: v.$blanco;
            font-size: 2.4rem;
            margin-bottom: 1rem ;
            @include m.tablet {
                margin: 0;
            }

            &:hover{                //& elimina el espacio en el css para que los elementos se compilen juntos
                color: v.$amarillo;
            }
    }
}

