/* Configuración de la fuente base para el tamaño de texto.
   1 rem (root em) es igual al 10% del tamaño de fuente del elemento raíz, 
   que generalmente es el elemento HTML. 
   Al establecerlo en 62.5%, se hace que 1 rem sea igual al 10px, 
   lo que facilita los cálculos de tamaño de fuente.
*/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

/* Establece 'box-sizing' como 'border-box' para todos los elementos en la página.
   Esto asegura que el tamaño de un elemento incluirá su borde y relleno, 
   en lugar de ser solo el contenido, lo que es la configuración predeterminada.
*/
*,
*:before,
*:after {
  box-sizing: inherit;
}

/* Configuración de la fuente principal y tamaño de fuente para el cuerpo (body) de la página. */
body {
  font-family: "Montserrat", sans-serif; /* Establece la fuente principal definida en la variable v.$fuente_principal. */
  font-size: 1.6rem; /* Establece el tamaño de fuente a 1.6 rem (root em). */
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  scroll-padding-top: 8.6rem;
}

/* Estilos para los párrafos (p). */
p {
  font-size: 2rem; /* Establece el tamaño de fuente de los párrafos a 2 rem. */
  color: #000000; /* Establece el color de texto utilizando la variable v.$negro. */
  line-height: 1.5; /*interlineado*/
}

/* Estilos para el contenedor con clase "contenedor". */
.contenedor {
  width: 95%; /* Establece el ancho del contenedor al 95% del ancho disponible. */
  max-width: 120rem; /* Establece el ancho máximo del contenedor a 120 rem. */
  margin: 0 auto; /* Centra horizontalmente el contenedor en la página. */
}

/* Estilos para los enlaces (a). */
a {
  text-decoration: none; /* Elimina la decoración de subrayado de los enlaces. */
}

/* Estilos para los títulos h1, h2, y h3. */
h1, h2, h3 {
  margin: 0 0 5rem 0; /* Establece el margen en la parte inferior de los títulos. */
}

/* Estilos para h1 (encabezado de nivel 1). */
h1 {
  font-size: 4rem; /* Establece el tamaño de fuente de h1 a 4 rem. */
}

/* Estilos para h2 (encabezado de nivel 2). */
h2 {
  font-size: 3.6rem; /* Establece el tamaño de fuente de h2 a 3.6 rem. */
}

/* Estilos para h3 (encabezado de nivel 3). */
h3 {
  font-size: 3rem; /* Establece el tamaño de fuente de h3 a 3 rem. */
}

img {
  max-width: 100%; /*se utiliza para establecer el ancho máximo de un elemento especificado*/
  width: 100%;
  height: auto;
  display: block; /*hace que el comportamiento del elemento sea como un bloque.*/
}

.header {
  background-color: #662984;
}
@media (min-width: 768px) {
  .header.fijo {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.1rem 0.2rem 0.3rem #333;
  }
}
.header h1 {
  color: #FFFFFF;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
@media (min-width: 768px) {
  .header .contenido-header {
    padding: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    gap: 2rem;
  }
}
.navegacion-principal a {
  display: block;
  text-align: center;
  color: #FFFFFF;
  font-size: 2.4rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin: 0;
  }
}
.navegacion-principal a:hover {
  color: #FEF46D;
}

.video {
  /* Establece la posición relativa del elemento, posicionándolo con respecto a su posición original en el flujo del documento. */
  position: relative;
  /* Establece la altura máxima del elemento en 70 unidades rem. */
  max-height: 70rem;
  /* Establece la altura mínima del elemento en 50 unidades rem. */
  min-height: 50rem;
  /* Controla el comportamiento de desbordamiento del contenido dentro del elemento. 
  En este caso, cualquier contenido que exceda la altura máxima se ocultará. */
  overflow: hidden;
}
.video .overlay {
  /* Establece la posición absoluta del elemento, posicionándolo con respecto al contenedor más cercano con posición relativa o al borde de la ventana. */
  position: absolute;
  /* Establece un fondo con degradado lineal que va desde un tono de púrpura semitransparente a un tono de rosa más claro. */
  background: rgb(126, 2, 195);
  background: linear-gradient(270deg, rgba(126, 2, 195, 0.5) 0%, rgba(255, 0, 110, 0.3) 100%);
  /* Establece la altura y el ancho del elemento al 100% del tamaño del contenedor padre. */
  height: 100%;
  width: 100%;
  /* Establece el elemento como un contenedor flexible con dirección de columna, lo que significa que sus hijos se colocarán en una columna de arriba a abajo. */
  display: flex;
  flex-direction: column;
  /* Centra el contenido verticalmente dentro del contenedor flexible. */
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 992px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  /* Establece la posición de apilamiento del elemento en 10. 
  Un valor mayor de z-index coloca el elemento encima de elementos con un z-index menor. */
  z-index: 10;
  z-index: 10;
}
.contenido-video h2 {
  color: #FFFFFF;
  /* Establece el tamaño de fuente del texto a 7 rem (unidades relativas al tamaño de la fuente del elemento raíz). */
  font-size: 7rem;
  /* Elimina los márgenes por defecto del elemento. */
  margin: 0;
}
.contenido-video p {
  /* Establece el color de fondo utilizando una variable llamada $rosa definida en el contexto de un preprocesador como Sass o Less. */
  background-color: #f53756;
  /* Define el comportamiento de visualización del elemento como en línea, pero permite ajustar márgenes y dimensiones como un bloque. */
  display: inline-block;
  /* Establece el relleno (padding) del elemento, proporcionando espacio alrededor del contenido. */
  padding: 1rem 2rem;
  /* Establece el color del texto utilizando una variable llamada $blanco definida en el contexto de un preprocesador como Sass o Less. */
  color: #FFFFFF;
  /* Establece la negrita (bold) del texto. */
  font-weight: 700;
  /* Establece el tamaño de fuente del texto. */
  font-size: 2.4rem;
  /* Elimina los márgenes por defecto del elemento. */
  margin: 0;
}

/* Estilo para la sección 'sobre-festival' */
.sobre-festival {
  /* Establece el relleno (padding) superior e inferior del contenedor */
  padding: 5rem 0;
  /* Utiliza un mixin para aplicar estilos específicos en dispositivos tipo tablet */
}
@media (min-width: 768px) {
  .sobre-festival {
    /* Utiliza otro mixin para aplicar un sistema de cuadrícula con 2 columnas y un espacio entre ellas de 5rem */
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

/* Estilo para la sección 'contenido-festival' */
.contenido-festival {
  /* Establece el elemento como un contenedor flexible con dirección de columna y contenido centrado verticalmente */
  display: flex;
  flex-direction: column;
  justify-content: center;
  /* Utiliza estilos específicos para tablet al agregar un margen superior de 5rem */
  /* Estilos para el elemento 'h2' dentro de 'contenido-festival' */
  /* Estilos para el elemento con la clase 'fecha' dentro de 'contenido-festival' */
}
@media (min-width: 768px) {
  .contenido-festival {
    margin-top: 5rem;
  }
}
.contenido-festival h2 {
  /* Establece un margen inferior de 2rem para 'h2' */
  margin-bottom: 2rem;
}
.contenido-festival .fecha {
  /* Establece el color del texto utilizando una variable $rosa definida en el contexto del preprocesador */
  color: #f53756;
  /* Establece la negrita (bold) del texto */
  font-weight: 700;
}

.lineup {
  background-color: #f53756;
  padding: 5rem 0;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #FFFFFF;
}
.lineup .dia {
  text-transform: uppercase;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.escenarios-contenedor p {
  color: #000000;
}
.escenarios-contenedor .escenario {
  padding-top: 2rem;
}
.escenarios-contenedor .escenario p:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}

.edm .nombre-escenario:after {
  background-image: url(../img/icono_edm.png);
}

.rock .nombre-escenario:after {
  background-image: url(../img/icono_rock.png);
}

.calendario {
  margin: 0;
  padding: 0;
  list-style: none;
}
.calendario li {
  padding: 2.5rem;
  font-weight: 700;
}
.calendario li span {
  font-weight: 400;
}

.bg-amarillo {
  background-color: #FEF46D;
}
.bg-amarillo li:nth-child(odd) {
  background-color: #fef03a;
}

.bg-verde {
  background-color: #4CB8B3;
}
.bg-verde li:nth-child(odd) {
  background-color: #3b9691;
}

.galeria {
  padding: 5rem 0;
}
.galeria h3 {
  text-align: center;
}

.galeria-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.btn-cerrar {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: #FFFFFF;
  color: #000000;
  display: grid;
  place-items: center;
  font-weight: 700;
}
.btn-cerrar:hover {
  cursor: pointer;
}

.boletos {
  padding: 5rem 0;
}
.boletos h3 {
  text-align: center;
  color: #FFFFFF;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .boletos .contenedor-boletos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pase {
  color: black;
}
.pase .nombre-pase {
  text-align: center;
  font-size: 2.8rem;
  color: #FFFFFF;
}
.pase-header, .pase-footer {
