}
.pase-header {
  background-color: #f53756;
}
.pase-header p {
  text-transform: uppercase;
  font-size: 1.6rem;
}
.pase-body {
  background-color: #FFFFFF;
}
.pase-body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pase-body ul li,
.pase-body ul :nth-child(odd) {
  background-color: #FFFFFF;
  padding: 1rem;
