}
.pase-footer {
  background-color: #752F97;
}
.pase-footer p {
  font-size: 2.4rem;
  font-weight: 700;
}

.site-footer {
  background-color: #f53756;
  padding: 1rem;
}
.site-footer p {
  margin: 0;
  text-align: center;
  color: #FFFFFF;
}